/**
 * 返利订单列表
 * @author 张恩铭
 * @date 20170301
 * 页面引用
 *  <script src="__PUBLIC__/js/lithe.js"
 data-config="config.js"
 data-debug="true"
 data-main="conf/rebate/index.js">
 </script>
 */
define('conf/rebate/index.js', function(require, exports, module) {
    const $ = require('$');
    const ajax = require('utils/async/ajax.js');
    const UI = require('UI/dialog/alert');
    const Vue = require('vue');
    // 这个插件的文档 https://github.com/ximan/dropload
    require('vendors/dropload.js');
    const laz = require('utils/async/lazyload.js');

    let $orderList = $('.orders');
    let $noOrders = $('.no-orders');
    let pageSize = 10;

    init();

    function init() {
        setFilters();
        Vue.component('order-list-more', {
            props: {
                orders: {
                    type: Array,
                },
            },
            template: '#order-list-more-template',
        });
        let vm = new Vue({
            el: 'body',
            data: {
                currentPageNum: 1,
                orderListData: [],
                currentStatus: 'ALL',
                pageSize,
                reqDisabled: false,
            },
            init() {
                $orderList.dropload({
                    scrollArea: window,
                    domUp: {
                        domClass: 'dropload-up',
                        domRefresh: '<div class="dropload-refresh">↓ Pull To Refresh</div>',
                        domUpdate: '<div class="dropload-update">↑ Release To Refresh</div>',
                        domLoad: '<div class="dropload-load"><i class="loading"></i>Refreshing...</div>',
                    },
                    domDown: {
                        domClass: 'dropload-down',
                        domRefresh: '<div class="dropload-refresh">↑ Pull To Load More</div>',
                        domLoad: '<div class="dropload-load"><i class="loading"></i>Loading...</div>',
                        domNoData: '<div class="dropload-noData">There\'re No More Orders</div>',
                    },
                    loadUpFn(me) {
                        refreshCurrentTab(me);
                    },
                    loadDownFn(me) {
                        if (vm.reqDisabled) {
                            UI.alertSecond('No More Orders');
                            me.resetload();
                        } else {
                            ajax.query({
                                url: `/api/ext/trade/orders?status=${vm.currentStatus}&pageNum=${++vm.currentPageNum}&pageSize=${vm.pageSize}`,
                                success(res) {
                                    if (res.code === 200) {
                                        if (res.data.orders.length > 0) {
                                            vm.orderListData = vm.orderListData.concat(res.data.orders);
                                            laz.lazyload();
                                        } else {
                                            UI.alertSecond('No More Orders');
                                            vm.reqDisabled = true;
                                        }
                                    } else {
                                        UI.alertSecond(res.message);
                                    }
                                    me.resetload();
                                },
                            });
                        }
                    },
                });
            },
            methods: {
                filterOrders(evt) {
                    let $this = $(evt.target);
                    let type = $this.data('anchor');
                    if (type !== undefined) {
                        this.currentStatus = type;
                        refreshCurrentTab();
                    }
                },
            },
        });

        /*
        刷新当前页面的数据，提供参数时重置 dropload
        */
        function refreshCurrentTab(me) {
            vm.reqDisabled = false;
            $orderList.children('[data-first-screen]').remove();
            vm.orderListData = [];
            vm.currentPageNum = 0;
            ajax.query({
                url: `/api/ext/trade/orders?status=${vm.currentStatus}&pageNum=${++vm.currentPageNum}&pageSize=${vm.pageSize}`,
                success(res) {
                    if (res.code === 200) {
                        if (res.data.orders.length > 0) {
                            $noOrders.css('display', 'none');
                            vm.orderListData = res.data.orders;
                            laz.lazyload();
                        } else {
                            $noOrders.css('display', 'block');
                        }
                    } else {
                        UI.alertSecond(res.message);
                    }
                    // 插件文档规定每次数据加载完必须通过 resetload 重置
                    me !== undefined && me.resetload();
                },
            });
        }
    }

    function setFilters() {
        Vue.filter('time', function(time, separator) {
            var tz = (new Date()).getTimezoneOffset();
        	var date = time ? new Date(time - tz * 60000) : new Date();
            var year = date.getFullYear();
        	var month = (date.getMonth() + 1) > 9 ? (date.getMonth() + 1) : '0' + (date.getMonth() + 1);
        	var day = date.getDate() > 9 ? date.getDate() : '0' + date.getDate();
        	// var hour = date.getHours() > 9 ? date.getHours() : '0' + date.getHours();
        	// var minute = date.getMinutes() > 9 ? date.getMinutes() : '0' + date.getMinutes();
        	// var second = date.getSeconds() > 9 ? date.getSeconds() : '0' + date.getSeconds();
        	var dateStr = '';
        	if (separator) {
        		dateStr += day + separator + month + separator + year;
        	} else {
        		dateStr += day + '/' + month + '/' + year;
        	}
        	return dateStr;
        });
        Vue.filter('addFullStop', function(string){
            return string += '. ';
        });
        Vue.filter('money', function(money){
            return (money / 100);
        });
    }

});
